html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
}
body.fixed-body {
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  body.navbar-scroll {
    padding-top: 8.6rem;
  }
}

h1, h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

h3, h4 {
  font-family: "Lato", sans-serif;
  font-weight: 500;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.6rem;
  margin-bottom: 5rem;
  font-weight: bold;
}

a {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  text-decoration: none;
}

p {
  font-family: "Roboto", sans-serif;
  font-size: 2rem;
}

.contenedor {
  width: min(95%, 120rem);
  margin: 0 auto;
}

.animate {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s, transform 0.5s;
}

.animate.show {
  opacity: 1;
  transform: translateY(0);
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
}

.header--background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  background-color: transparent;
}

@media screen and (min-width: 768px) {
  .fijo {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgb(0, 0, 0);
    box-shadow: 0.3rem 0.3rem 1rem 0.1rem #333;
    animation: aparecer 200ms ease-in normal;
  }
}

@keyframes aparecer {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.header {
  color: #ffffff;
}
.header h1 {
  text-align: center;
  padding: 2rem 0;
}
.header h1 span {
  font-size: 2.5rem;
}
@media screen and (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding: 1rem 0;
}
.navbar a {
  color: #ffffff;
  text-decoration: none;
}
.navbar a:hover {
  color: #FEF464;
}
@media screen and (min-width: 768px) {
  .navbar {
    flex-direction: row;
  }
}

.video {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .video {
    height: 100vh;
  }
}
.video video {
  width: auto;
  height: auto;
  display: block;
}
@media screen and (min-width: 768px) {
  .video video {
    width: max-content;
    height: 120vh;
  }
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .overlay {
    justify-content: flex-start;
    text-align: left;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #FFFFFF;
  font-size: 6rem;
}
.contenido-video p {
  background-color: #1c8b00;
  color: #FFFFFF;
  font-weight: bold;
  display: inline-block;
  padding: 1rem 2rem;
}

.sobre-festival {
  padding: 4rem 0;
}
@media screen and (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    justify-content: center;
    align-items: center;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-top: 3rem;
}
@media screen and (min-width: 768px) {
  .contenido-festival {
    text-align: left;
    margin-top: 0;
  }
}
.contenido-festival p:last-of-type {
  font-size: 1.8rem;
  line-height: 1.5;
}
.contenido-festival p:first-of-type {
  color: #1c8b00;
  font-weight: bold;
}
.contenido-festival span {
  color: #1c8b00;
}

.conocelos {
  background-color: #1c8b00;
  background-image: url("https://freepngimg.com/thumb/leaves/37361-1-green-leaves-photos.png");
  background-repeat: repeat-y;
  background-size: contain;
  background-blend-mode: multiply;
  backdrop-filter: brightness(0);
  padding: 5rem 0;
}
.conocelos h3 {
  text-align: center;
  color: #FFFFFF;
}

.animales-contenedor {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 3rem;
  max-width: 100rem;
}
@media screen and (min-width: 768px) {
  .animales-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.animal-card {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 5rem 3rem;
  background-color: whitesmoke;
  border-radius: 10px;
  -webkit-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  height: 100%;
}
.animal-card .animal-nombre {
  font-size: 3rem;
  margin-bottom: 2rem;
}
.animal-card .animal-nombre::before {
  content: "";
  display: inline-block;
  width: 0.5rem;
  height: 2rem;
  background-color: #1c8b00;
  margin: 0 1rem;
}
@media screen and (min-width: 768px) {
  .animal-card .animal-nombre {
    font-size: 4rem;
    margin-bottom: 2rem;
  }
  .animal-card .animal-nombre::before {
    content: "";
    display: inline-block;
    width: 0.5rem;
    height: 3rem;
    background-color: #1c8b00;
    margin: 0 1rem;
  }
}
@media screen and (min-width: 992px) {
  .animal-card .animal-nombre {
    font-size: 5rem;
    margin-bottom: 2rem;
  }
  .animal-card .animal-nombre::before {
    content: "";
    display: inline-block;
    width: 0.5rem;
    height: 4rem;
    background-color: #1c8b00;
    margin: 0 1rem;
  }
}
.animal-card .section-title {
  width: fit-content;
  margin: 1rem 0 0 0;
  padding-inline: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  display: inline-block;
  background-color: #1c8b00;
  color: #FFFFFF;
}
.animal-card .section-curiosidades p {
  font-size: 1.4rem;
  margin: 0.2rem 0;
}
.animal-card .section-danger {
  color: red;
  font-weight: bold;
  align-self: flex-end;
  justify-self: end;
}
.animal-card p, .animal-card div {
  align-self: flex-start;
  text-align: left;
  font-size: 1.7rem;
  margin: 0.5rem 0;
}

.card-img-top {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  object-fit: cover;
  position: absolute;
  top: 3rem;
  right: 2rem;
  transition: all 300ms ease-in;
}

.card-img-top:hover {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  top: 0;
  right: 0;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}
.galeria ul {
  padding: 2rem 0;
}

.galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media screen and (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media screen and (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media screen and (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}

.img-animal {
  position: relative;
  -webkit-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
}

.img-corta img {
  height: 100%;
  object-fit: cover;
}

.img-larga {
  grid-row: span 2;
}
.img-larga img {
  height: 100%;
  object-fit: cover;
}

.videos-contenedor {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
  place-content: center center;
}
@media screen and (min-width: 768px) {
  .videos-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.videos-contenedor iframe {
  -webkit-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  width: 100%;
  max-width: 56rem;
  height: 100%;
  min-height: 360px;
}

.video-1 {
  display: none;
}
@media screen and (min-width: 768px) {
  .video-1 {
    display: block;
  }
}

.overlay-imagen {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
}
.overlay-imagen picture {
  display: block;
  max-width: 120rem;
  margin: 0;
  align-self: flex-end;
}
.overlay-imagen .btn-cerrar {
  padding: 2rem;
  background-color: #000000;
  color: #FFFFFF;
  border-radius: 50%;
  font-weight: 700;
  cursor: pointer;
  align-self: flex-start;
}

.ayuda {
  padding: 5rem 0;
  background-color: #FFFFFF;
}
.ayuda h3 {
  text-align: center;
  color: #000000;
}

.contenedor-ayudas {
  padding: 2rem;
}
@media screen and (min-width: 768px) {
  .contenedor-ayudas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.pase p {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}
.pase .pase-dia {
  font-size: 2rem;
  padding: 2rem 0;
}

.pase-header {
  background-color: #1c8b00;
}
.pase-header p {
  font-size: 1.6rem;
  padding: 1rem 0;
  text-transform: uppercase;
}

.ayuda-body {
  background-color: transparent;
}
.ayuda-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: transparent;
}
.ayuda-body li {
  position: relative;
  width: fit-content;
  color: #FFFFFF;
  background-color: #1c8b00 !important;
  font-weight: 700;
  padding: 0.2rem 1rem;
  font-size: 1.6rem;
  margin: 1rem 0;
}
.ayuda-body li::before {
  position: absolute;
  left: -2rem;
  top: 1rem;
  content: "";
  display: block;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #1c8b00;
}

.form-container {
  border: 3px solid #1c8b00;
  border-radius: 10px;
  padding: 2rem;
  -webkit-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  box-shadow: 10px 10px 20px 0px rgba(0, 0, 0, 0.44);
  background-color: #FFFFFF;
}
.form-container p {
  text-align: center;
  margin-top: 0.5rem;
  margin-bottom: 2rem;
}

.form-subscribe label {
  font-size: 1.4rem;
  font-family: "Roboto", sans-serif;
}
.form-subscribe input, .form-subscribe textarea {
  font-size: 1.5rem;
  font-family: "Roboto", sans-serif;
}
.form-subscribe .btn-primary {
  background-color: #1c8b00;
  font-size: 1.5rem;
  border: 1px solid #000000;
  font-family: "Roboto", sans-serif;
}
.form-subscribe .btn-primary:active {
  background-color: #125800;
}

.site-footer {
  background-color: #1c8b00;
  padding: 2rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #000000;
  font-size: 1.5rem;
}
.site-footer div {
  display: flex;
  align-items: center;
  justify-content: center;
}
.site-footer a {
  color: #000000;
  transition: all 300ms ease;
  margin: 1rem;
}
.site-footer a:hover {
  color: #FFFFFF;
}